.main {
    text-align: center;
    width: 80%;
    margin: 0 auto;
    font-size: 48px;
    font-weight: bolder;
    height: 1280px;
  }
  
  .headerCss{
    height: 20%;
    border: solid 3px #eee;
  }
  .secAside{
    display: flex;
    justify-content: space-around;
    height: 50%;
    border: solid 3px #eee;
  }
  .navCss{
    height: 15%;
    border-right: solid 3px #eee;
    border-left: solid 3px #eee;
  }
  .darkModeBtn{
    border: 0;
    padding: 10px;
    font-size: 32px;
    font-weight: bolder;
  }
  .sectionCss{
    width: 70%;
    border-right: solid 3px #eee;
  }
  .asideCss{
    width: 30%;
  }
  .footerCss{
    height: 15%;
    border-right: solid 3px #eee;
    border-left: solid 3px #eee;
  }